//
// Dropzone
//
// Overwrite/Extend styles
// --------------------------------------------------

.dropzone {
  min-height: 200px;
  background-color: $body-bg-light;
  border: 0.125rem dashed $input-border-color;
  border-radius: $border-radius-lg;

  .dz-message {
    margin: 4rem 0;
    font-size: 1rem;
    font-style: italic;
    font-weight: $font-weight-bold;
    color: $body-color;
  }

  &:hover {
    background-color: $white;
    border-color: $primary;

    .dz-message {
      color: $primary;
    }
  }
}
